@function override-fallback($intent, $replace: '') {
  $resolved: #{$intent};
  $comma: str-index($resolved, ',');

  @if $comma {
      $transform: str-slice($resolved, 1, $comma - 1);
      @if $replace != '' {
          @return $transform + ", " + $replace + ")";
      }
      @return $transform + ")";
  }

  @return $resolved;
}
